@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --green-main: #0fc684;
  --dark-main: #181f32;
  --white-main: white;
}
a {
  cursor: pointer;
}
.App {
  background-color: #181f32;
  background-color: var(--dark-main);
  height: 100vh;
  font-family: "Montserrat", sans-serif;
  z-index: -2;
}
/* Navbar */
.Navbar {
  display: flex;
  justify-content: space-between;
  color: white;
  height: 50px;
  position: fixed;
  width: 100%;
  z-index: 11;
  align-items: center;
  background-color: #181f32;
  background-color: var(--dark-main);
}
.download-resume-img:hover {
  stroke: #0fc684;
  stroke: var(--green-main);
}
.navbar__left {
  display: flex;
  width: 100px;
  justify-content: flex-end;
  font-weight: 600;
  font-size: large;
}
.navbar__right {
  display: flex;
  width: 40%;
  justify-content: space-between;
  margin-right: 40px;
}
.navbar__right div {
  padding: 10px;
}
.navbar__right a {
  cursor: pointer;
  /* border-bottom: 1px solid var(--green-main); */
  transition: all 0.2s ease-in-out;
  text-decoration: none;
  color: white;
}
.hamburger-menu {
  display: none;
}
/* Sections */
.section {
  height: calc(100vh);
}

/* Home */
.Home {
  background-color: #181f32;
  background-color: var(--dark-main);
  display: flex;
  position: relative;
  z-index: 10;
}
.home_left {
  height: 100%;
  z-index: 1000;
}
.home__social {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}
.home__social img {
  width: 35px;
  padding: 20px;
}

.home__right {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  color: white;
}
.home__right__top {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease-in-out;
}
.home__right a {
  z-index: 10;
}
.masked-image {
  width: 50em;
  transition: all 0.5s ease-in-out;
}

/* About */
.About {
  display: flex;
  align-items: center;
  color: white;
  background-color: #181f32;
  background-color: var(--dark-main);
  flex-direction: column;
  padding: 50px;
}
.about__title {
  font-size: 2em;
  font-weight: 700;
  text-transform: uppercase;
  color: #0fc684;
  color: var(--green-main);
  border-bottom: 5px solid #0fc684;
  border-bottom: 5px solid var(--green-main);
}
.about__description {
  width: 60%;
  font-size: 2em;
  text-transform: capitalize;
  line-height: 1.3em;

  font-weight: 600;
  border: 5px solid #0fc684;
  border: 5px solid var(--green-main);
  padding: 50px;
  margin-top: 100px;
  border-bottom: 100px solid #0fc684;
  border-bottom: 100px solid var(--green-main);
}
/* active class */
.active {
  color: #0fc684;
  color: var(--green-main);
  font-weight: 600;
  transform: scale(1.3);
  border-bottom: 2px solid #0fc684;
  border-bottom: 2px solid var(--green-main);
}

.background__image {
  z-index: -11;
  position: absolute;
  height: 100%;
  width: 100%;
  background: url(../../static/media/profile.50013f3e.png);
  background-repeat: no-repeat;
  /* background-position: center; */
  background-size: contain;
  mix-blend-mode: saturation;
  opacity: 0.05;
}

.hide {
  display: none;
}
@media only screen and (max-width: 600px) {
  .Navbar {
    flex-direction: column;
    margin: 0;
    align-items: center;
    justify-content: center;
    height: auto;
    background-color: #181f32;
  }
  .navbar__left {
    justify-content: space-between;
    padding: 0px 30px;
    width: 100vw;
  }
  .hamburger-menu {
    display: block;
    padding: 10px;
    width: 40px;
    cursor: pointer;
  }
  .logo {
    padding: 10px;
  }
  .navbar__right {
    flex-direction: column;
    align-items: center;
    padding: 10px;
  }
  .navbar__right a {
    padding: 20px;
  }
  .masked-image {
    width: 20em;
  }
  .text-top {
    font-size: 30px !important;
  }
  .text-bottom {
    font-size: 16px !important;
  }
}

